import React from "react";
import { Link } from "react-router-dom";
import { gsap } from "gsap";
import { TextPlugin } from "gsap/TextPlugin";

// import style
import style from "./../styles/Cv.module.css";

gsap.registerPlugin(TextPlugin);

function Cv() {
  return (
    <section className={style.wrap}>
      <div>
        <div>
           
          <div className={style.scrolling}>
            <ul>
              <li style={{ color: "#ffcafd" }}> </li>
              <li style={{ color: "#63dbff" }}> </li>
              <li style={{ color: "#b5ff63" }}></li>
              <li style={{ color: "#ffda6e" }}> </li>
              <li style={{ color: "#ffcafd" }}> </li>
            </ul>
          </div>
           ?
        </div>
        <Link className="btn_arrow" to="https://www.rallit.com/resumes/270797@limyoursun/%EC%9E%84%EC%84%A0" target="_blank">
          <span> /  </span>
          <em aria-hidden="true"> </em>
        </Link>
      </div>
    </section>
  );
}

export default Cv;
