import React, { useEffect } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

// import style
import style from "./../styles/Introduce.module.css";

gsap.registerPlugin(ScrollTrigger);

function Introduce() {
  useEffect(() => {
      gsap.matchMedia().add("(min-width: 1025px)", () => {
        gsap.from(".intro", {
          duration: 0.1,
          scrollTrigger: {
            trigger: "#Main",
            start: "top top",
            onEnter: () => gsap.to(".intro", { y: -185, duration: 0.5 }),
            onLeaveBack: () => gsap.to(".intro", { y: 0, duration: 0.2 }),
          },
        });
        gsap.to(".intro_text", {
          scrollTrigger: {
            trigger: "#Main",
            start: "top top",
            onEnter: () => gsap.to(".intro_text", { text: "!", delay: .5, duration: 1}),
            onLeaveBack: () => gsap.to(".intro_text", { text: "!", delay: .5, duration: 1})
          },
        });
      })
    })
  return (
    <section className={style.wrap}>
      <div className="intro">
        <p className="intro_text">!</p>
        <p>3  .</p><br/><br className="m_con"/>
        <p> ,     ,</p>
        <p>    <br className="m_con"/>    .</p><br/><br className="m_con"/>
        <p>   , <br className="m_con"/>      .</p><br className="m_con"/><br className="m_con"/>
        <p>      !</p>
      </div>
    </section>
  );
}

export default Introduce;
